@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
	font-family: "Raleway", sans-serif;
	width: 100vw;
	height: 100vh;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
/* 	font: inherit; */
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.pageContainer {
	display: flex;
}

@media (max-width: 420px) {
	.pageContainer {
		display: block;
	}
}

.detailsContainer {
	width: 74vw;
	height: 100vh;
	box-sizing: border-box;
	padding: 4.5vh 125px;
	background-color: #100e1d;
	position: relative;
	transition: opacity 2s;
}

.unitsContainer {
	display: flex;
	justify-content: flex-end;
}

.unit {
	width: 2.2vw;
	height: 2.2vw;
	font-size: 16px;
	font-weight: 500;
	text-align: center;
	line-height: 2.2vw;
	background-color: #585676;
	color: #e7e7eb;
	border-radius: 50%;
	margin-left: 12px;
}

.unit:hover {
	background-color: #e7e7eb;
	color: #110e3c;
	cursor: pointer;
}

.selectedUnit {
	background-color: #e7e7eb;
	color: #110e3c;
}

.weekContainer {
	display: flex;
	justify-content: space-between;
	margin-top: 4vh;
}

#sectionTitle {
	font-weight: bold;
	font-size: 23px;
	font-style: normal;
	color: #e7e7eb;
	margin-top: 8vh;
}

.currCardContainer {
	display: flex;
	justify-content: space-between;
	margin-top: 4vh;
	margin-bottom: 4vh;
}

#loadingCloud {
	position: absolute;
	display: block;
	width: 200px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	opacity: 0.03;
	animation: pulse 4.2s infinite ease-in;
}

@keyframes pulse {
	0%,
	100% {
		opacity: 0.03;
	}
	50% {
		opacity: 1;
	}
}

#credits {
	color: #616475;
	position: absolute;
	bottom: 9px;
	left: 50%;
	font-size: 13px;
	text-align: center;
	transform: translateX(-50%);
}

#ghLink {
	text-decoration: underline;
	color: #616475;
}

#ghLink:hover {
	color: #e7e7eb;
}

@media (max-width: 420px) {
	.detailsContainer {
		width: 100vw;
		height: 100%;
		min-height: 180vh;
		padding: 20px;
	}

	.unit {
		width: 40px;
		height: 40px;
		line-height: 40px;
	}

	.weekContainer {
		display: grid;
		justify-items: center;
		align-items: center;
		grid-template-columns: 1fr 1fr;
	}

	.currCardContainer {
		flex-direction: column;
		align-items: center;
		margin-top: 0;
	}

	#credits {
		position: relative;
		margin: 0 auto;
		margin-top: 80px;
	}
}

.detailsCard {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	max-width: 21vw;
	width: 21vw;
	height: 9vw;
	color: #e7e7eb;
	background-color: #1e213a;
}

#title {
	font-size: 18px;
}

#mainInfo {
	font-size: 37px;
	font-weight: bold;
}

.progressBarInfoContainer {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 80%;
}

.progressBarInfo {
	color: #a09fb1;
	font-size: 13px;
	margin-bottom: -1.2vh;
}

#progressBar {
	position: relative;
	width: 80%;
	height: 9px;
	background-color: #e7e7eb;
	border-radius: 80px;
	overflow: hidden;
}

#progressBarFilling {
	display: block;
	height: 100%;
	border-radius: 80px;
	background-color: #ffec65;
	overflow: hidden;
}

.percentageContainer {
	position: relative;
	width: 80%;
}

#percentageUnit {
	position: absolute;
	right: 0;
	margin-top: -1vh;
	font-size: 12px;
}

.feelsLikeSubtitle {
	display: flex;
	justify-content: space-between;
	width: 40%;
}

#maxTemp {
	color: #a09fb1;
}

@media (max-width: 420px) {
	.detailsCard {
		min-width: 300px;
		min-height: 150px;
		margin-bottom: 5px;
		margin-top: 30px;
	}
}

.weekCard {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	box-sizing: border-box;
	padding: 15px 20px;
	background-color: #1e213a;
	height: 12.5vw;
	width: 8.4vw;
	font-size: 15px;
	margin-top: 0.4vw;
}

.cardInfo {
	color: #e7e7eb;
}

.cardIcon {
	width: 4.5vw;
	padding-right: 1.2vw;
	padding-bottom: 15px;
}

.cardBottom {
	display: flex;
	justify-content: space-between;
	width: 85%;
}

#tempMin {
	color: #a09fb1;
}

@media (max-width: 420px) {
	.weekCard {
		margin-top: 20px;
		margin-bottom: 20px;
		width: 75%;
		min-height: 200px;
	}

	.cardIcon {
		width: 70px;
		padding-right: 1.8vw;
	}

	.cardInfo {
		font-size: 16px;
	}

	.cardBottom {
		font-size: 16px;
	}
}

.forecastContainerBigDiv {
	background-color: #1e213a;
}

.forecastContainer {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 26vw;
	height: 100vh;
	background-color: #1e213a;
}

.searchBarInactive {
	display: flex;
	justify-content: space-between;
	width: 85%;
	height: 5.2vh;
}

#searchBtnInactive {
	width: 10vw;
	height: 5.2vh;
	font-size: 15px;
	background-color: #6e707a;
	color: #e7e7eb;
	border-style: none;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

#searchBtnInactive:hover {
	box-shadow: 0px 0px 4px rgba(255, 255, 255, 0.25);
	cursor: pointer;
}

#searchBtnInactive:active {
	box-shadow: 0px 0px 4px rgba(255, 255, 255, 0.25) inset;
}

#currentLocationIcon {
	width: 5.2vh;
	background: #6e707a;
	padding-top: 0.9vh;
	border-radius: 50%;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

#currentLocationIcon:hover {
	box-shadow: 0px 0px 4px rgba(255, 255, 255, 0.25);
	cursor: pointer;
}

#currentLocationIcon:active {
	box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25) inset;
}

.displayWeather {
	display: block;
	position: relative;
	width: 29vw;
	height: 28vh;
}

.fakeAfter {
	position: absolute;
	content: "";
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: url(/weather-app/static/media/cloudBackground.f88244c2.png);
	background-size: 26vw 28vh;
	background-position: 410% 80%;
	opacity: 0.06;
	transition: opacity 150ms ease-in, background-position 2300ms ease-in-out;
}

#weatherIcon {
	display: block;
	width: 9.5vw;
	margin: 0 auto;
	opacity: 0;
	transition: 2.8s opacity ease-in;
}

#tempDisplay {
	color: #e7e7eb;
	font-size: 75px;
}

span {
	font-size: 38px;
}

#description {
	color: #a09fb1;
	font-size: 27px;
	font-weight: 600;
	text-align: center;
}

#date {
	color: #88869d;
	font-size: 14px;
	margin-bottom: -6vh;
}

#locationIcon {
	min-width: 15px;
	width: 0.8vw;
}

.location {
	color: #88869d;
	font-weight: 600;
	font-size: 14px;
}

#closeBtn {
	font-size: 40px;
	color: #e7e7eb;
	align-self: flex-end;
	margin-right: 45px;
	margin-bottom: 25px;
	transition: color 150ms ease-out;
}

#closeBtn:hover {
	color: #585676;
	cursor: pointer;
}

.searchBarContainer {
	display: flex;
	justify-content: space-between;
}

.inputContainer {
	position: relative;
	box-sizing: border-box;
	border: 1px solid #e7e7eb;
	padding: 10px 40px;
	height: 40px;
	margin-right: 10px;
}

#searchIcon {
	position: absolute;
	left: 7px;
	top: 50%;
	width: 15px;
	transform: translate(0%, -50%);
}

#inputArea {
	border-style: none;
	background-color: #1e213a;
	color: #616475;
	font-size: 13.5px;
}
#inputArea:focus {
	outline: none;
}

#searchBtnActive {
	border-style: none;
	background-color: #3c47e9;
	color: #e7e7eb;
	padding: 2px 20px;
	font-size: 13.5px;
	font-weight: 600px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	transition: background-color 120ms;
}

#searchBtnActive:hover {
	background-color: #353fc4;
	cursor: pointer;
}

#searchBtnActive:focus {
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25) inset;
}

.historyList {
	width: 80%;
}

input,
button,
form {
	font-family: inherit;
}

@media (max-width: 420px) {
	.forecastContainer {
		width: 100vw;
		justify-content: space-between;
		padding: 18px 5px 65px 5px;
		box-sizing: border-box;
	}

	.displayWeather {
		width: 100vw;
	}

	.fakeAfter {
		background-size: 100vw;
	}

	#weatherIcon {
		width: 33vw;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	.searchBarInactive {
		width: 93%;
	}

	#searchBtnInactive {
		width: 178px;
	}

	#currentLocationIcon {
		width: 9.3vw;
		height: 7.5vw;
	}

	.searchBarContainer {
		width: 95%;
		justify-content: space-evenly;
	}

	.inputContainer {
		margin-right: 5px;
	}
}

.searchItemContainer {
	width: 100%;
	padding: 18px 10px;
	box-sizing: border-box;
	margin-top: 8vh;
	margin-bottom: -7vh;
	position: relative;
	border: 1px solid transparent;
}

#locationName {
	color: #e7e7eb;
	font-size: 16px;
}

#arrow {
	position: absolute;
	right: 10px;
	width: 10px;
	transform: rotate(-90deg);
	opacity: 0;
	transition: opacity 200ms, transform 400ms;
}

.searchItemContainer:hover {
	border: 1px solid #616475;
	cursor: pointer;
}

.searchItemContainer:hover #arrow {
	opacity: 1;
	transform: rotate(90deg);
}

